.cases {
	position: relative;
	padding: 15vh 30px 310px 50px;
	padding-top: calc(100px + (180 - 100)*((100vh - 750px)/(960 - 750)));
	height: 100%;
	max-height: 100%;
	width: 595px;
	@include css-lock(14,20,1024,1440);
	&__content {
		max-width: 402px; }
	&__title {
		margin-bottom: 3vh;
		max-width: 275px; }
	&__slider {
		position: absolute;
		left: 364px;
		bottom: 0;
		width: calc(100vw - ((100vw - 1440px) / 2 + 364px) ); }
	&__slider-wrap {
		padding-top: 80px;
		overflow: hidden; } }
.slider {
	position: relative;
	margin-left: 372px;
	width: 230px;
	font-size: 0;
	.is-pointer-none & {
		pointer-events: none; }
	.slick-list {
		overflow: visible; }
	.slick-list {
		overflow: visible; }
	.slick-slide {
		img {
			position: relative;
			transform: translateX(0);
			display: block; }
		&.is-prev {
			img {
				transform: translateX(100%);
				transition: transform 0s;
				transition-delay: 0;
				animation: slideLeft 0.4s cubic-bezier(.47,.01,.18,.99) forwards; } }
		&.is-next {
			img {
				transform: translateX(-100%);
				transition: transform 0s;
				animation: slideRight 0.6s cubic-bezier(.47,.01,.18,.99) forwards;
				animation-delay: 0.2s !important; } }
		.is-animate-next & {
				$animation-delay: 0s;

				@for $i from 1 through 50 {
					&:nth-of-type(#{$i}) {
						img {
							transition-delay: $animation-delay;
							animation-delay: $animation-delay;
							$animation-delay: $animation-delay + 0.05s; } } }
				&.is-active {
					img {
						transform: translateX(100%);
						transition: transform 0s;
						animation: none; } }
				img {
					transition: transform .7s cubic-bezier(.47,.01,.18,.99); } }
		.is-animate-prev & {
			$animation-delay: 0s;

			@for $i from 1 through 50 {
				&:nth-of-type(#{$i}) {
					img {
						transition-delay: $animation-delay;
						animation-delay: $animation-delay;
						$animation-delay: $animation-delay - 0.05s; } } }
			&.is-active {
				img {
					transform: translateX(-100%);
					transition: transform 0s;
					animation: none; } }
			img {
				transition: transform 1s cubic-bezier(.47,.01,.18,.99); } } }
	&__prev,
	&__next {
		position: absolute;
		top: -80px;
		margin-left: -372px;
		width: 75px;
		height: 80px;
		text-align: center;
		background-color: red;
		&:after {
			content: "";
			background: $white;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			opacity: 0;
			transition: width 1s cubic-bezier(.47,.01,.18,.99), opacity 1s cubic-bezier(.47,.01,.18,.99);
			transition-delay: .15s; }
		&:active:after {
			width: 0px;
			opacity: 1;
			transition: 0s; }
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -9px 0 0 -5px;
			font-size: 18px; } }
	&__prev {
		left: 155px;
		color: $white;
		background-color: $black;
		&:after {
			left: 0; } }
	&__next {
		left: 230px;
		color: $black;
		background-color: $white;
		&:after {
			background-color: $black;
			right: 0; } } }

@keyframes slideLeft {
	0% {
		transform: translateX(100%); }
	50% {
		opacity: .8; }
	70% {
		opacity: 0; }
	100% {
		transform: translateX(0);
		opacity: 0; } }
@keyframes slideRight {
    0% {
        transform: translateX(-100%); }
    100% {
        transform: translateX(0); } }

.slider-text {
	position: absolute;
	z-index: 1;
	top: 80px;
	left: 230px;
	width: 372px;
	background-color: $white;
	.slick-arrow {
		display: none !important; }
	&__slide {
		margin-bottom: 40px;
		padding: 20px 25px 45px;
		width: 100%;
		@include css-lock(150,300,1024,1440,height);
		color: #817D85;
		background-color: $white;
		@include vertical;
		&_black {
			color: $white;
			background-color: $black; } }
	&__inner {
		letter-spacing: -0.02em;
		font-size: 16px;
		text-align: center;
		line-height: 1.625;
		vertical-align: middle;
		display: inline-block; }
	&__title {
		margin-bottom: 12px;
		font-family: $Futuras;
		@include f(20px,500,1.5);
		color: $gray;
		letter-spacing: 0;
		text-transform: uppercase;
		.slider-text__slide_black & {
			color: $white; } } }

.small-slider {
	position: absolute;
	top: 80px;
	left: 0;
	z-index: 1;
	width: 230px;
	.slick-slide {
		.is-animate-next & {
			$animation-delay: 0s;

			@for $i from 1 through 50 {
				&:nth-of-type(#{$i}) {
					img {
						transition-delay: $animation-delay;
						animation-delay: $animation-delay;
						$animation-delay: $animation-delay + 0.05s; } } }
			&.is-active {
				img {
					transform: translateX(100%);
					transition: transform 0s;
					animation: none; } }
			img {
				transition: transform .7s cubic-bezier(.47,.01,.18,.99); } }
		.is-animate-prev & {
			$animation-delay: 0s;

			@for $i from 1 through 50 {
				&:nth-of-type(#{$i}) {
					img {
						transition-delay: $animation-delay;
						animation-delay: $animation-delay;
						$animation-delay: $animation-delay - 0.07s; } } }
			&.is-active {
				img {
					transform: translateX(-100%);
					transition: transform 0s;
					animation: none; } }
			img {
				transition: transform 1s cubic-bezier(.47,.01,.18,.99); } }
		img {
			position: relative;
			transform: translateX(0);
			display: block; }
		&.is-prev {
			img {
				transform: translateX(100%);
				transition: transform 0s;
				transition-delay: 0;
				animation: slideLeftSmall 0.4s cubic-bezier(.47,.01,.18,.99) forwards; } }
		&.is-next {
			img {
				transform: translateX(-100%);
				transition: transform 0s;
				animation: slideRightSmall 0.5s cubic-bezier(.47,.01,.18,.99) forwards;
				transition-delay: 0s !important;
				animation-delay: 0s !important; } } } }

@keyframes slideLeftSmall {
	0% {
		transform: translateX(100%);
		opacity: 1; }
	100% {
		opacity: 0;
		transform: translateX(0); } }
@keyframes slideRightSmall {
	0% {
		transform: translateX(-100%);
		opacity: 1; }
	100% {
		transform: translateX(0);
		opacity: 0; } }
