.fp-slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 3;
  overflow: hidden;
}

.fp-slider .inner {
  height: 100%;
}

[data-fp-slide] {
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 86px;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: visibility .4s, opacity .4s;
}

[data-fp-slide].is-active {
  visibility: visible;
  opacity: 1;
}

.gravity {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.gravity:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  transform: translate(-50%);
  min-width: 1440px;
  min-height: 960px;
  width: 100vw;
  height: 100vw;
  pointer-events: none;
  background-image: radial-gradient(circle at top, #fafdf9 0%, rgba(167, 155, 178, 0) 64%);
}

.gravity__arrow-wrap {
  position: absolute;
  width: 70px;
  left: 50%;
  margin-left: -35px;
  z-index: 5;
  height: 70px;
  bottom: 50px;
  font-size: 0;
  text-align: center;
}

.gravity__arrow-wrap.is-animate {
  animation: arrowAnimWrap 2.3s ease-out alternate forwards;
}

.gravity__arrow-wrap.is-animate .gravity__arrow {
  animation: arrowAnim 2.3s ease-out alternate forwards;
}

.gravity__arrow-wrap.is-animate .gravity__arrow svg {
  animation: arrowAnimSvg 2.3s ease-out alternate forwards;
}

.gravity__arrow-wrap.is-animate .gravity__arrow-inner {
  animation: arrowAnimInner 2.3s ease-out alternate forwards;
}

.gravity__arrow-inner {
  position: relative;
}

.gravity__arrow-btn {
  position: relative;
}

.gravity__arrow {
  position: relative;
  width: 70px;
  height: 70px;
  font-size: 0;
  z-index: 30;
  text-align: center;
  background-color: transparent;
  border-radius: 50%;
  background-color: #fff;
  transform: translateY(50px) scale(0);
  vertical-align: top;
  display: inline-block;
}

.gravity__arrow svg {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -7px 0 0 -14px;
  width: 28px;
  height: 14px;
}

.gravity__arrow svg path {
  stroke: #A79BB1;
}

@keyframes arrowAnimWrap {
  0% {
    transform: translate(0, 0);
  }
  40% {
    transform: translate(0px, -150px);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes arrowAnim {
  0% {
    transform: translate(0px, 100px) scale(0) rotate(0deg);
    transform-origin: 200% 200%;
  }
  100% {
    transform: translate(0, 0) scale(1) rotate(-720deg);
    transform-origin: 50% 50%;
  }
}

@keyframes arrowAnimSvg {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(720deg);
  }
}

@keyframes arrowAnimInner {
  0% {
    transform: translateX(0);
  }
  30% {
    transform: translateX(-50px);
  }
  100% {
    transform: translateX(0);
  }
}

