  0% {
    transform: translateX(100%);
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateX(0);
  }
}

@keyframes slideRightSmall {
  0% {
    transform: translateX(-100%);
    opacity: 1;
  }
  100% {
    transform: translateX(0);
    opacity: 0;
  }
}

[data-stagger] {
  position: relative;
  opacity: 0;
  display: block;
}

[data-stagger="left"] {
  transform: translateX(-400px) scale(1.5);
}

[data-stagger="right"] {
  transform: translateX(400px) scale(1.5);
}

[data-stagger="up"] {
  transform: translateY(-400px) scale(1.5);
}

[data-stagger="bottom"] {
  transform: translateY(400px) scale(1.5);
}

[data-stagger="up-left"] {
  transform: translate(-400px, -400px) scale(1.5);
}

[data-stagger="bottom-left"] {
  transform: translate(-400px, 400px) scale(1.5);
}

[data-stagger="up-right"] {
  transform: translate(900px, -900px) scale(1.5);
}

[data-stagger="bottom-right"] {
  transform: translate(600px, 600px) scale(1.5);
}

[data-stagger="from-down"] {
  transform: translateY(140px);
}

.about {
  position: relative;
  float: right;
  padding: 75px;
  height: 100%;
  padding-top: calc(100px + (180 - 100)*((100vh - 750px)/(960 - 750)));
  width: 645px;
  font-size: 20px;
  font-size: calc(14px + (20 - 14) * ((100vw - 1024px) / (1440 - 1024)));
}

@media (max-width: 1024px) {
  .about {
    font-size: 14px;
  }
}

@media (min-width: 1440px) {
  .about {
    font-size: 20px;
  }
}

.about__content {
  max-width: 432px;
}

.about__title {
  margin-bottom: 2vh;
}

.about__info {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 300px;
  min-height: 300px;
  width: 300px;
  width: calc(250px + (300 - 250) * ((100vw - 1024px) / (1440 - 1024)));
  min-height: 300px;
  min-height: calc(250px + (300 - 250) * ((100vw - 1024px) / (1440 - 1024)));
  font-size: 30px;
  font-size: 30px;
  font-size: calc(25px + (30 - 25) * ((100vw - 1024px) / (1440 - 1024)));
  color: #fff;
  letter-spacing: 0;
  text-align: center;
  line-height: 1.3333;
  background-color: #191926;
  display: table;
}

@media (max-width: 1024px) {
  .about__info {
    width: 250px;
  }
}

@media (min-width: 1440px) {
  .about__info {
    width: 300px;
  }
}

@media (max-width: 1024px) {
  .about__info {
    min-height: 250px;
  }
}

@media (min-width: 1440px) {
  .about__info {
    min-height: 300px;
  }
}

@media (max-width: 1024px) {
  .about__info {
    font-size: 25px;
  }
}

@media (min-width: 1440px) {
  .about__info {
    font-size: 30px;
  }
}

.no-touch .about__info:hover .icon {
  transform: translateX(5px);
}

.about__info-inner {
  padding: 0 34px;
  vertical-align: middle;
  display: table-cell;
}

.about__info-inner .icon {
  margin-left: 4px;
  fill: #fff;
  vertical-align: middle;
  font-size: 18px;
  transition: transform .3s;
}

.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  padding: 30px;
  visibility: hidden;
  overflow: auto;
  text-align: center;
  font-size: 0;
}

.popup:before {
