body {
	font-family: $Futuras;
	color: rgba($black, 0.5);
	@include f(20px,500,1.5);
	min-width: 1260px;
	@include font-smoothing;
	letter-spacing: 0;
	background-color: $white;
	&.is-dark {
		color: rgba($white, .5);
		background-color: $darkBg;
		.title {
			color: $white; } }
	&.no-min-width {
		min-width: inherit; } }

.title {
	font-family: $Futuras;
	@include f(30px,500,1.333);
	color: $black;
	@include css-lock(21,30,1024,1440);
	&_bold {
		font-weight: 900; } }
.title-bg {
	font-family: $Futuras;
	@include f(280px,500,1);
	color: rgba($black-mine,.03);
	@include css-lock(196,280,1024,1440);
	position: absolute;
	text-transform: uppercase;
	text-align: center;
	z-index: -1;
	left: 0;
	right: 0;
	top: 0; }

.title-bg_lg {
	font-family: $Futuras;
	@include f(340px,500,1);
	color: rgba($black-mine,.03);
	@include css-lock(64,340, 320, 1440);
	position: absolute;
	text-transform: uppercase;
	text-align: center;
	z-index: -1;
	left: 0;
	right: 0;
	top: 0; }

.title-roboto {
	font-family: $Roboto;
	font-weight: 700;
	font-size: 48px;
	line-height: 1.2;
	text-transform: uppercase;
	color: #d0b560; }


.light {
	font-weight: 300; }

.prev-head {
	margin-top: 60px; }

.text-upper {
	text-transform: uppercase; }
