[data-obj-big-position="top"],
[data-obj-big-position="top-triangle"] {
	top: -60px;
	left: 50%;
	transform: translateX(-50%);
	@include xs {
		display: none; } }
[data-obj-big-position="top-triangle"] {
	top: -101px;
	@include xs {
		display: block; } }

[data-obj-big-position="bottom"],
[data-obj-big-position="bottom-triangle"] {
	bottom: -98px;
	left: 50%;
	transform: translateX(-50%);
	@include xs {
		display: none; } }
[data-obj-big-position="bottom-triangle"] {
	bottom: -110px; }


[data-obj-big-position="bottom-right"] {
	right: -190px;
	bottom: -230px;
	@include xs {
		display: none; } }

