.bamboo-header {
	transition: 1500ms;
	overflow: hidden;
	position: relative;
	min-height: 880px;
	min-width: 320px;
	@include sm {
		min-height: 480px; }

	&__background-image {
		transition: 1500ms;
		overflow: hidden;
		position: absolute;
		width: 100%;
		height: 880px;
		background: url("../img/case1/case-bamboo-logo.png") center;
		background-size: cover;
		&:before {
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: rgba( #242437, 0.64); }
		@include sm {
			height: 480px; } }

	&__inner-top {
		transition: 1500ms;
		display: flex;
		flex-direction: column;
		height: 880px;
		position: relative;
		&:after {
			transition: 1000ms;
			content: '';
			width: 2px;
			height: 69px;
			background-color: rgba($white, 0.5);
			position: absolute;
			bottom: 0;
			left: 50%; }
		@include md {
			&:after {
				height: 20px; } }
		@include sm {
			height: 480px;
			&:after {
				height: 68px; } } }

	&__inner-bottom {
		transition: 1500ms;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		@include sm {
			position: relative; } }

	&__bg {
		pointer-events: none;
		position: absolute; }

	&__bg_line-out {
		transition: 1500ms;
		transform: rotate(-65deg);
		left: -280px;
		bottom: 230px;
		width: 575px;
		height: 150px;
		@include md {
			left: -330px; }
		@include sm {
			width: 275px;
			height: 78px;
			left: -124px;
			bottom: 0; } }

	&__bg_line {
		transition: 1000ms;
		right: 0;
		left: auto;
		position: absolute;
		background-color: rgba(#4B4B90, 0.44);
		width: 0;
		height: 150px;
		@include sm {
			height: 78px; } }

	&__bg_line-full {
		left: 0;
		right: auto;
		width: 575px;
		@include sm {
			width: 275px;
			height: 78px; } }

	&__bg_half-circle {
		opacity: 0;
		transform-origin: 0 50%;
		transform: rotate(45deg);
		top: 42px;
		right: -210px;
		.icon {
			transition: 1500ms;
			fill: rgba(#9DD97A, 0.2);
			width: 400px;
			height: 776px; } }

	&__bg_half-circle-full {
		transition: 1500ms;
		opacity: 1;
		transform: rotate(-45deg);
		@include md {
			right: -400px; }
		@include sm {
			top: 80px;
			right: -92px;
			.icon {
				width: 150px;
				height: 300px; } } }

	&__bg_circle {
		transition: 1500ms;
		bottom: -130px;
		right: -170px;
		.icon {
			fill: rgba(#D3D9DD, 0.4);
			width: 0;
			height: 0; } }

	&__bg_circle-full {
		.icon {
			transition: 1500ms;
			width: 260px;
			height: 260px; }
		@include md {
			bottom: -160px; }
		@include sm {
			right: -62px;
			bottom: -94px;
			.icon {
				width: 130px;
				height: 130px; } } }


	&__bg_circle-lg {
		transition: 1500ms;
		top: -888px;
		left: -396px;
		.icon {
			fill: rgba(#D3D9DD, 0.2);
			width: 0;
			height: 0; } }

	&__bg_circle-lg-full {
		.icon {
			transition: 1500ms;
			width: 1097px;
			height: 1097px; }
		@include md {
			left: -560px; }
		@include sm {
			left: -1000px; } }

	&__bg_wave {
		transition: 1000ms;
		transform: translateX(-50%);
		left: 50%;
		bottom: 83px;
		.icon {
			width: 80px;
			height: 7px; }
		@include md {
			bottom: 20px; }
		@include sm {
			bottom: 55px; } }


	&__logo {
		transition: 1500ms;
		transform: translateX(-50%)translateY(-50%);
		position: absolute;
		top: 50.5%;
		left: 50%;
		.icon {
			transition: 1500ms;
			width: 480px;
			height: 160px; }
		@include sm {
			top: 55%;
			.icon {
				width: 279px;
				height: 93px; } } }


	&__prev {
		position: relative;
		margin-top: 0;
		padding-top: 60px;
		.btn-prev__text {
			margin: 13px 0 0 18px; }
		@include sm {
			.btn-prev__text {
				margin: 0 0 0 18px; } } }

	&__list-wrapper {
		display: flex;
		justify-content: flex-end;
		margin: auto 85px 38px auto;
		.technologies__item:not(:first-child) {
			margin-left: 22px; }
		@include sm {
			margin: 14px 0 0;
			left: 0;
			right: 0;
			justify-content: center; } }

	&__list {
		display: flex;
		@include sm {
			flex-wrap: wrap; } }

	&__item {
		display: flex;
		flex-direction: column;
		margin-left: 28px;
		@include sm {
			align-items: center;
			width: 50%;
			margin: 26px 0 0; } }


	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $white;
		width: 64px;
		height: 64px;
		border-radius: 50%;
		.icon {
			width: 32px;
			height: 32px; }
		@include sm {
			box-sizing: border-box;
			border: 2px $dark solid; } }

	&__name {
		font-family: $Futuras;
		font-weight: 500;
		color: $white;
		font-size: 14px;
		text-align: center;
		@include sm {
			color: $dark; } } }


